
export default {
	onLaunch: function() {
		console.log('App Launch')
		// 云开发设置
		if (!wx.cloud) {
			console.error('请使用 2.23 或以上基础库使用云能力')
		} else {
			wx.cloud.init({
				traceUser: true,
				env: 'lin-serve-lh' //环境id
			})
			console.log('云开发初始化成功')
		}
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
